import React, { useEffect, useState } from 'react';
import Hero from '../components/Hero/Hero.js';
import AboutMe from '../components/AboutMe/AboutMe.js';
import Languages from '../components/Languages/Languages.js';
import Portfolio from '../components/Portfolio/Portfolio.js';
import Contact from '../components/Contact/Contact.js';
import './main.scss';

const Main = ({setYuriPhoto, yuriPhoto, removeInitYuri, setRemoveInitYuri, setImgClass, imgClass, animComplete, setAnimComplete, desktop}) => {

  const [position, setPosition] = useState(null);
  const [info, setInfo] = useState('');
  const [nationality, setNationality] = useState(null);
  const [mainStyle, setMainStyle] = useState(null)

  useEffect(() => {
    
    const getLocation = async () => {
      try {
        const response = await fetch("https://api.geoapify.com/v1/ipinfo?apiKey=168e3d69578f432e9ef85dd9d79f5c53");

        if (response.ok) {
          const result = await response.json();
          setInfo(result);
          setPosition(result.location)

          if (result.country) {
            const countryName = result.country.name;

            const result1 = await fetch(`https://restcountries.com/v3.1/name/${countryName}`)

            if (result1.ok) {
              const countries = await result1.json();
              console.log(countries[0].flags.png)

              if (countries) {
              setNationality(countries[0]);
              }
            }
          }
        }
      } catch (error) {
        console.log('error', error);
      }
    };

    getLocation();

  }, []);

  useEffect(() => {
   
   if (desktop) {setMainStyle({
      border: "1px solid #74C991",
      maxWidth: 920, 
      boxShadow: "4px 4px 20px rgba(0, 0, 0, 0.621)",
      borderRadius: 12,
      margin: "0 auto 30px auto"
      })} else {setMainStyle(null)}
  }, [desktop])

  return (
    <main style={desktop ? {margin: "20px auto"} : null}>
      <Hero position={position} nationality={nationality}/>
      <div className='main-div' style={mainStyle}>
        <AboutMe 
        info={info} 
        nationality={nationality} 
        yuriPhoto={yuriPhoto} 
        setYuriPhoto={setYuriPhoto} 
        removeInitYuri={removeInitYuri} 
        setRemoveInitYuri={setRemoveInitYuri}
        setImgClass={setImgClass}
        imgClass={imgClass}
        animComplete={animComplete}
        setAnimComplete={setAnimComplete}
        />
        <h2>Tools</h2>
        <Languages />
        <h2>Portfolio</h2><br/>
        <Portfolio />
        <h2>Contact</h2><br/>
        <Contact />
      </div>
    </main>
  );
};

export default Main;
