import React, { useEffect, useState } from 'react';
import './about-me.scss';
import {hellos} from '../../Main/greetings.js'
import { debounce } from '@mui/material';

const AboutMe = ({info, nationality, setYuriPhoto, yuriPhoto, removeInitYuri, setRemoveInitYuri, imgClass, setImgClass, animComplete, setAnimComplete}) => {
  const [aboutHeight, setAboutHeight] = useState(null);
  const [profileHeight, setProfileHeight] = useState(null)
  const [profileInView, setProfileInView] = useState(false)
  const [index1, setIndex1] = useState(0);
  const [firstP, setFirstP] = useState('');
  const [isInView, setIsInView] = useState(false);
  const [greeting, setGreeting] = useState('')
  const [clickMe, setClickMe] = useState(false)
  

  const aboutText = `

    <h2>Hello World...</h2><br/>
    <p>${nationality && nationality.demonyms ? "and " + (greeting ? greeting : "greetings") + " to you, my " + nationality.demonyms.eng.f + (info.country.flag ? info.country.flag : "") + " friend!<br/><br/>" : ""}
    
    I'm Yuri Koller, a web developer driven by a passion for creative problem-solving and a love for learning. My journey into web development stems from a desire to blend artistic expression with technical innovation.<br /><br />
    
    With a background in audio engineering, I bring a unique perspective to my work, approaching projects with a meticulous attention to detail and a commitment to excellence. I specialize in crafting engaging user experiences using a variety of technologies, including React, Node.JS, MongoDB, and more.<br /><br />
    
    I thrive in collaborative environments and enjoy the process of transforming ideas into tangible solutions. Whether it's designing a sleek interface or optimizing backend functionality, I'm dedicated to delivering high-quality work that exceeds expectations.<br /><br />

    Let's connect and explore how we can bring your ideas to life on the web!</p>`;

    const getAboutHeight = debounce (() => {
      setAboutHeight(document.querySelector('#about').getBoundingClientRect());
    }, 50);

  useEffect(() => {
    console.log('hi-3')

    const handleScroll = () => {
      if (
        aboutHeight &&
       ( aboutHeight.top >= 0 ||
        (aboutHeight.top >= 0 && aboutHeight.left >= 0 &&
        aboutHeight.right <= window.innerWidth &&
        aboutHeight.bottom <= window.innerHeight))
      ) {
        setIsInView(true)
      } else {
        getAboutHeight()}
    }


    window.addEventListener('load', handleScroll)

   if (!isInView) {
    window.addEventListener('scroll', handleScroll);
   }

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('load', handleScroll)
    };
  }, [aboutHeight]);

  useEffect(() => {
    console.log('hi-4')
    
    if (profileInView && !yuriPhoto && !removeInitYuri) {
      setImgClass("yuri-cartoon profile-appear")
    } else if (profileInView 
      && removeInitYuri 
      && imgClass !== 'yuri-cartoon set' 
      && imgClass !== 'yuri-cartoon shrinking' 
      && imgClass !== 'yuri-cartoon growing' 
      && imgClass !== 'yuri-photo shrinking' 
      && imgClass !== 'yuri-photo growing') {
    
        setImgClass('yuri-cartoon set')
      
    } else {setImgClass('yuri-cartoon')}

  }, [profileInView, removeInitYuri])


  useEffect(() => {

    console.log('hi-5')

    const handleScroll = () => {
      setProfileHeight(document.querySelector('.about').getBoundingClientRect());
      if (
        profileHeight &&
        ((profileHeight.top >= 0 &&
        profileHeight.left >= 0 &&
        profileHeight.right <= window.innerWidth)
        || profileHeight.bottom === window.innerHeight)
      ) {
        setProfileInView(true)
        setTimeout(() => {
        setRemoveInitYuri(true)},400)
      }}

    window.addEventListener('load', handleScroll)

   if (!profileInView) { 
    window.addEventListener('scroll', handleScroll);
   }

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('load', handleScroll)
    };
  }, [profileHeight]);

  useEffect(() => {
    console.log('hi-6')
    if (nationality) {
      const hello = Object.entries(hellos).filter((item) => {

      return Object.values(item)[0] === (Object.values(nationality.languages)[0] === "English" ? Object.values(nationality.languages)[1] : Object.values(nationality.languages)[0])})
  
   if (hello.length > 0) {
    setGreeting(hello[0][1].greetings[0].toLowerCase().replace(/\s*\([^)]*\)/, ''))
};
  }
  
  }, [nationality])

  useEffect(() => {
    console.log('hi-7')
    let interval;

    if (isInView && firstP.length < aboutText.length) {
      interval = setInterval(() => {
        setFirstP(aboutText.slice(0,[index1]));

        setIndex1((prev) => prev + 1);
      }, 10);
    }

    return () => {
      clearInterval(interval);
    };
  }, [isInView, firstP, index1, aboutText]);

  const clickHeadImg = () => {
    const img = document.querySelector(".yuri-div > img")
      
      img.style.opacity = null
    if ((imgClass === "yuri-cartoon shrink" 
    || imgClass === "yuri-photo grow") && !animComplete) {
      setAnimComplete(true)
      setImgClass('yuri-photo shrink')
      setTimeout(() => {
        setYuriPhoto(!yuriPhoto); 
        setImgClass('yuri-cartoon grow')
        setTimeout(() => {setAnimComplete(false)}, 300)
      }, 300)
    } else if ((imgClass === "yuri-photo shrink" 
    || imgClass === "yuri-cartoon grow"
    || imgClass === "yuri-cartoon set"
    ) && !animComplete) {
    setAnimComplete(true)
    setImgClass('yuri-cartoon shrink')
    setTimeout(() => {
      setYuriPhoto(!yuriPhoto); 
      setImgClass('yuri-photo grow')
      setTimeout(() => {setAnimComplete(false)}, 300)
    }, 300)
    }}

    const showclickMe = () => {
     if (!imgClass.includes('shrink') && !animComplete) {
      setClickMe(true)}
    }

    const removeclickMe = () => {
      setClickMe(false)
      
    
    }

    useEffect(() => {
      console.log('hi-8')
      if (imgClass.includes('shrink')) {
        setClickMe(false)
      }
    }, [imgClass])

  return (
    <div className='about'>
      <div className={`yuri-div ${profileInView ? 'div-appear' : ""}`} onClick={clickHeadImg} onMouseOver={showclickMe} onMouseOut={removeclickMe}>
      <img className={imgClass}  src={yuriPhoto ? "./YuriPortraitNoBGcopy.png" : "./Images/Yuricartoon.webp"} alt='yuri-cartoon' draggable="false" />
      {clickMe && <div className={`click-me ${clickMe ? "click-on" : "click off"}`}><img className='speech-bubble' src="./Images/speech-bubble1.png" alt='speech-bubble'/><h2>Click Me</h2></div>}
</div>
      <div id='about' dangerouslySetInnerHTML={{ __html: firstP }}>
      </div>
    </div>
  );
};

export default AboutMe;
