import React from 'react'
import './footer.scss'
import FavoriteIcon from '@mui/icons-material/Favorite';
import { languages } from '../components/Languages/language-info';
import { socialLinks } from '../socialLinks';


const Footer = () => {
  
  return (
    <footer>
      <div className='social'>
        <p>Stay in touch</p>
        <div className='social-links'>
        {socialLinks.map((e,i) => <a className="footer-socials" href={e.url} key={i}><e.icon style={{ fontSize: 36}} className="social-icon" /></a>)}
        </div>
      </div>
      <div className='foot-msg'><p>Built from scratch with <FavoriteIcon className="heart" /> using {Object.values(languages).filter((e) => e.name === "React" || e.name === "NodeJS" || e.name === "Sass" || e.name === "Git").map((item, i) => <img className='foot-tools' src={item.url} key={i} alt={item.name}/>)}</p></div>
    </footer>
  )
}

export default Footer
