import React, { useEffect, useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import './header.scss'
import ClearIcon from '@mui/icons-material/Clear';

const Header = ({setYuriPhoto, yuriPhoto, setImgClass, imgClass, animComplete, setAnimComplete, desktop, revealMenu, setRevealMenu}) => {
  const [aboveOrBelow, setaboveOrBelow] = useState(false);
  const [menuType, setMenuType] = useState(null)
 
  

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY >= 80) {
        setaboveOrBelow(true)
      } else {setaboveOrBelow(false)}
    };

    if (window.scrollY >= 80) {
      console.log('hi-1')
      setaboveOrBelow(true)
    } 

    
    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const smoothScrollTo = (id) => {
    let offset = -170; 
    
    if (id === 'about') {
      const element = document.querySelector(`.${id}`);
      const topPos = element.getBoundingClientRect().top + window.scrollY + offset;
      window.scrollTo({ top: topPos, behavior: 'smooth' });
    } else {
      const element = document.getElementById(id);
      const topPos = element.getBoundingClientRect().top + window.scrollY + offset;
      window.scrollTo({ top: topPos, behavior: 'smooth' });
    }
  };
  

  const clickHeadImg = () => {
    if ((imgClass === "yuri-cartoon shrink" 
    || imgClass === "yuri-photo grow") && !animComplete) {
      setAnimComplete(true)
      setImgClass('yuri-photo shrink')
      setTimeout(() => {
        setYuriPhoto(!yuriPhoto); 
        setImgClass('yuri-cartoon grow')
        setAnimComplete(false)
      }, 300)
    } else if ((imgClass === "yuri-photo shrink" 
    || imgClass === "yuri-cartoon grow"
    || imgClass === "yuri-cartoon set"
    ) && !animComplete) {
    setAnimComplete(true)
    setImgClass('yuri-cartoon shrink')
    setTimeout(() => {
      setYuriPhoto(!yuriPhoto); 
      setImgClass('yuri-photo grow')
      setAnimComplete(false)
    }, 300)
    }}

   
  return (
    <header className={aboveOrBelow ? 'above' : 'below'}>
      <nav>
        <div className='menu-left'>
          <div className='profile-div'>
            <img draggable="false"
            className={`${yuriPhoto ? "cartoon" : "real"} ${imgClass === "yuri-cartoon shrink" 
              || imgClass === "yuri-photo shrink" ? "profile-img-out" 
              : "profile-img-in"}`} 
              src={yuriPhoto ? "./Images/Yuricartoon.webp" 
              : "./YuriPortraitNoBG.png"} alt="profile" 
              style={yuriPhoto ? {width: 60, height: 60, marginLeft: -7} : null}
            onClick={clickHeadImg}/>
          </div>
          Yuri Koller
        </div>
        {desktop === false && <MenuIcon className='burger' onClick={() => {setRevealMenu(true)}} />}
        <ul className={`${desktop ? "menu" : "mobile menu"} ${revealMenu && desktop === false ? 'revealed' : revealMenu === false && desktop === false ? 'unrevealed' : ""}`}>
          {revealMenu && <ClearIcon className='x' onClick={() => {setRevealMenu(false)}} />}
          <li onClick={() => {setRevealMenu(false)}}><a onClick={() => smoothScrollTo('about')}>About</a></li>
          <li onClick={() => {setRevealMenu(false)}}><a onClick={() => smoothScrollTo('portfolio')}>Portfolio</a></li>
          <li onClick={() => {setRevealMenu(false)}}><a onClick={() => smoothScrollTo('contact')}>Contact</a></li>
        </ul>
      </nav>
    </header>
  )
}

export default Header
