import React from 'react'
import './languages.scss'
import { languages } from './language-info'

const Languages = () => {

  return (
      <ul className='languages'>
       {languages.map((e, i) => <li 
       name={e.name.toLowerCase()} 
       key={i} 
       onMouseOver={() => {
        const modalID = document.querySelector(`#${e.name}-${i}`)
        modalID.style.visibility = "visible"
        modalID.style.opacity = "1"
        modalID.style.fontSize = "clamp(8px,2vw,20px)"

        const imgID = document.querySelector(`#${e.name.toLowerCase()}`)
        imgID.style.scale = 0.8;
        imgID.style.filter = "saturate(100%) brightness(100%) drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.445))";
      }
      }
       onMouseLeave={() => {
        const modalID = document.querySelector(`#${e.name}-${i}`)
        modalID.style.visibility = null
        modalID.style.opacity = null
        modalID.style.fontSize = null

        const imgID = document.querySelector(`#${e.name.toLowerCase()}`)
        imgID.style.scale = null
        imgID.style.filter = null
      }}
       >
        <img 
        id={e.name.toLowerCase()} 
        className="lang-img" 
        src={e.url} 
        alt={e.name}
        draggable="false"
        />
          <p className='modal' id={`${e.name}-${i}`}>{e.name}</p>
        </li>)}
      </ul>
  )
}

export default Languages
