
import Header from './Header/Header.js'
import Main from './Main/Main.js';
import Footer from './Footer/Footer.js'
import Sidebar from './components/Sidebar/Sidebar.js';
import { useState, useEffect } from 'react';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import './App.scss'

function App() {
  const [yuriPhoto, setYuriPhoto] = useState(false)
  const [removeInitYuri, setRemoveInitYuri] = useState(false)
  const [imgClass, setImgClass] = useState("yuri-cartoon")
  const [animComplete, setAnimComplete] = useState(false)
  const [desktop, setDesktop] = useState(null)
  const [revealMenu, setRevealMenu] = useState(null)
  const [pastHero, setPastHero] = useState(null)

  useEffect(() => {
    console.log('hi-9')

    const changeMenu = () => {if (window.innerWidth > 1099) {
       setDesktop(true)
       setRevealMenu(false)
     } else if (window.innerWidth <= 1099) {
       setDesktop(false)
       setRevealMenu(null)
     }}
     window.addEventListener('resize', changeMenu)
     
     if (desktop === null) {
      changeMenu()
     }

     return () => {
       window.removeEventListener('resize', changeMenu)
       
     }
   }, [])
  

   useEffect(() => {
    console.log('hi-10')
  const getHeroHeight = () => {const heroHeight = document.querySelector('.hero').getBoundingClientRect()
if (window.innerHeight > heroHeight.top &&
   
  window.innerHeight > heroHeight.bottom) {
      setPastHero(true)
   } else {setPastHero(false)}}


  if (!pastHero) {getHeroHeight()}
     
  window.addEventListener('scroll', getHeroHeight)
     return () => {
       window.removeEventListener('scroll', getHeroHeight)
     }
   }, [])

  return (
    <div className="App">
     <Header 
     yuriPhoto={yuriPhoto} 
     setYuriPhoto={setYuriPhoto} 
     setImgClass={setImgClass}
     imgClass={imgClass}
     animComplete={animComplete}
     setAnimComplete={setAnimComplete}
     desktop={desktop}
     setDesktop={setDesktop}
     revealMenu={revealMenu} setRevealMenu={setRevealMenu}
     />
     <Main 
     yuriPhoto={yuriPhoto} 
     setYuriPhoto={setYuriPhoto} 
     removeInitYuri={removeInitYuri} 
     setRemoveInitYuri={setRemoveInitYuri}
     setImgClass={setImgClass}
     imgClass={imgClass}
     animComplete={animComplete}
     setAnimComplete={setAnimComplete}
     desktop={desktop}
     />
     {desktop && <Sidebar/>}
     <Footer />
     {pastHero && 
     <ExpandLessIcon 
     className='top-button' 
     onClick={() => window.scrollTo({ top: document.querySelector(`.hero`).getBoundingClientRect().top, behavior: 'smooth' })}
     />}
    </div>
  );
}

export default App;
